.nav {
    grid-area: n;
    background-color: #E5E5E5;
    padding: 20px;
    font-weight: bold;


}
.items{
    background-color: #FFF;
    padding: 20px 20px;
    border-radius: 15px;
    margin-top: 0px;
}
.item{

    border-radius: 15px;
    list-style: none;
    transition: background-color .15s;
    box-sizing: border-box;


}
.item:hover{
    background-color: #F4F4F4;
    width: 100%;
}
.item a{
    color: #181818;
    text-decoration: none;
    width: 100%;
    height: 100%;
    display: inline-block;
    padding: 15px 15px;
}
.item a.active{
    color:#0077B5;
}